function ContadorTexto() {
  const [text, setText] = useState('');
  const [charCount, setCharCount] = useState(0);
  const [wordCount, setWordCount] = useState(0);

  useEffect(() => {
    setCharCount(text.length);
    setWordCount(text.split(/\s+/).filter(word => word.length > 0).length);
  }, [text]);

  function handleTextChange(event) {
    const newText = event.target.value;
    if (newText.length <= 150) {
      setText(newText);
    }
  }

  return (
    <div>
      <h1>Ingrese el Texto</h1>
      <textarea value={text} onChange={handleTextChange} />
      <div>Cantidad de letras: {charCount}/100</div>
      {charCount >= 100 && <div style={{ color: 'red' }}>Se esta excedindo la cantidad de texto ingresado(maximo 150)</div>}
      <div>Saltos en linea: {wordCount}</div>
    </div>
  );
}

export default ContadorTexto;
